<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="50%">
        <el-form :model="form" :rules="rules" ref="form" label-width="120px">
            <el-form-item label="" prop="name" >
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="storeType">
                <el-select v-model="form.storeType" filterable placeholder="">
                        <el-option label="" :value="1"></el-option>
                        <el-option label="" :value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="agencyId" v-if="form.storeType==1" >
                <el-select v-model="form.agencyId" placeholder="" @focus="agencyList" filterable :clearable="true">
                    <el-option label="" :value="0"></el-option>
                    <el-option
                        v-for="item in agencyInfoList"
                        :key="item.agencyId"
                        :label="item.agencyCompanyName"
                        :value="item.agencyId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="companyId" v-if="form.storeType==2" >
                <el-select v-model="form.companyId" placeholder="" @focus="companyList"  filterable :clearable="true">
                    <el-option
                        v-for="item in companyInfoList"
                        :key="item.companyId"
                        :label="item.name"
                        :value="item.companyId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="address">
                <el-input v-model="form.address"></el-input>
            </el-form-item>
            <el-form-item label="--" prop="areaLinkage">
                <region v-model="form.areaLinkage" :startLevel="startLevel" :endLevel="endLevel"></region>
            </el-form-item>
            <el-form-item label="" prop="longlat" placeholder=",">
                <el-input v-model="form.longlat" @blur="longlat"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm" type="primary"></el-button>
            <el-button @click="resetForm"></el-button>
        </span>
    </el-dialog>
</template>

<script>
import Region from '@/components/common/Region';

export default {
    name:'editorStore',
    props:['storeId','title'],
    components:{Region},
    data(){
        return {
            startLevel:2,//
            endLevel:4,//
            dialogVisible:false,
            companyInfoList:[],
            longlatFormat:false,//
            form: {
                address:'',//;
                agencyId:'',//Id;
                areaLinkage:[],//
                cityId:'',//Id;
                companyId:'',//Id;
                districtId:'',//Id;
                longlat:'',//;
                name:'',//;
                provinceId:'',//Id;
                storeType:'',//;
                townId:'',//Id;
            },
            // 
            rules: {
                companyId: [{ required: true, message: '', trigger: 'blur' }],
                agencyId:[{ required: true, message: '', trigger: 'blur' }],
                areaLinkage: [{ required: true, message: '', trigger: 'blur' }],
                address:[{ required: true, message: '', trigger: 'blur' }],
                name:[{ required: true, message: '', trigger: 'blur' }],
                storeType:[{ required: true, message: '', trigger: 'blur' }],
                longlat:[{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    created(){
        // ;
        this.agencyList();
        // ;
        this.companyList();
    },
    methods:{
        open() {
            let self =this;
            if(this.title==''){
                // ;
                this.$http.get(this.$apiUrl.store.add+"?id="+this.storeId)
                    .then(function(data){
                        self.form=data.data.data;
                        self.form.areaLinkage=[self.form.cityId,self.form.districtId,self.form.townId];
                    })
                    .catch(function(err){
                        console.log(err);
                    })
            }else{
                this.form= {
                    address:'',//;
                    agencyId:'',//Id;
                    areaLinkage:[],//
                    cityId:'',//Id;
                    companyId:'',//Id;
                    districtId:'',//Id;
                    longlat:'',//;
                    name:'',//;
                    provinceId:'',//Id;
                    storeType:'',//;
                    townId:'',//Id;
                }
            }
            this.dialogVisible = true;
        },
        longlat(){
            let longlatArray=this.form.longlat.split(',');
            var reg = /^[0-9]+.?[0-9]*$/;
            if(longlatArray.length==2&&reg.test(longlatArray[0])&&reg.test(longlatArray[1])){
                this.longlatFormat=true;
            }else{
                this.longlatFormat=false;
                this.$message({
                    message: '',
                    type: 'warning'
                });
            }
        },
        submitForm() {
            let self=this;
            this.longlat();
            this.$refs.form.validate((valid) => {
                if (valid) {
                    if(self.longlatFormat){
                        this.dialogVisible=false;
                        if(this.title==''){
                            if(this.form.areaLinkage.length){
                                this.form.cityId=this.form.areaLinkage[0];
                                this.form.districtId=this.form.areaLinkage[1];
                                this.form.townId=this.form.areaLinkage[2];
                            };
                            this.$http.post(this.$apiUrl.store.add,this.form)
                                .then(function(data){
                                    self.$message({
                                        message: '',
                                        type: 'success'
                                    });
                                    self.$emit('editSuccess',self.form);
                                })
                                .catch(function(err){
                                    self.$message({
                                        message: '',
                                        type: 'error'
                                    });
                                })
                        }else{
                            if(this.form.areaLinkage.length){
                                this.form.cityId=this.form.areaLinkage[0];
                                this.form.districtId=this.form.areaLinkage[1];
                                this.form.townId=this.form.areaLinkage[2];
                            };
                            this.$http.put(this.$apiUrl.store.add,this.form)
                                .then(function(data){
                                    self.$message({
                                        message: '',
                                        type: 'success'
                                    });
                                    self.$emit('addSuccess',self.form);
                                })
                                .catch(function(err){
                                    self.$message({
                                        message: '',
                                        type: 'error'
                                    });
                                });
                        };
                    }else{
                        self.$message({
                            message: '',
                            type: 'warning'
                        });
                    }
                    // ;
                    // this.$refs[formName].resetFields();
                } else {
                    self.$message({
                        message: '',
                        type: 'warning'
                    });
                    return false;
                }
            });
        },
        resetForm() {
            this.$refs.form.resetFields();
            this.dialogVisible=false;
        },
        // ;
        agencyList(){
            let self=this;
            this.$http.get(this.$apiUrl.agent.list)
            .then(function(data){
                self.agencyInfoList=data.data.data;
            })
            .catch(function(err){
                console.log(err,'');
            })
        },
        // ;
        companyList(){
            let self=this;
            // ;
            this.$http.get(this.$apiUrl.company.list)
                .then(function(data){
                    self.companyInfoList=data.data.data
                })
                .catch(function(err){
                    console.log(err,'err')
                })
        }
    }
}
</script>

<style>

</style>




